html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* prevent horizontal scrolling */
  box-sizing: border-box; /* make sizing predictable */
  width: 100%; /* ensure no body exceeds viewport */
}
*,
*::before,
*::after {
  box-sizing: inherit; /* inherit box-sizing */
}
